@import "~react-image-gallery/styles/css/image-gallery.css";

body {
  margin: 0;
  padding: 0;
  background-image: url('./background.png');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #222;
  font-family: 'Raleway', sans-serif;
}

#foreground{
  margin: 0;
  padding: 0;
  background-image: url('./foreground.png');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 95vw;
  height: 100vh;
  position: fixed;
  z-index: 5;
  pointer-events: none;
  color: #f00
}

ul, li {
  padding: 0;
  margin: 0;
  list-style: none;
}

li {
  padding: 3px 0;
  display: inline-block;
}

label {
  margin-left: 5px;
}

.header{
  background-color: rgba(0, 0, 0, .5);
  background-size: contain;
  background-repeat: repeat-y;
  width: auto;
  height: 80px;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 20;
}

a:hover{
  color:#fff;
  transition: color .3s ease-out
}

.header-logo{

}

.header-recalbox-container{

}

.header-recalbox{
  color: #ccc;
  padding-right: 10px;
  text-decoration: none;
  font-weight: bold;
  font-family: 'Raleway', sans-serif;
}

.header-recalbox-logo{
  margin-left: 10px;
  vertical-align: -40%;
}

.header-menu{

}

.header-menuItem{
  color: #ccc;
  padding: 10px;
  font-weight: bold;
  text-decoration: none;
  font-family: 'Raleway', sans-serif;
}

.app {

}

.app-header {
  letter-spacing: 1px;
  text-transform: uppercase;
}

.play-button {
  cursor: pointer;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  height: 60px;
  width: 100px;
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 5px;
}

.play-button:hover {
  background-color: rgba(0, 0, 0, 0.9);
}

.play-button:after {
  content: "";
  display: block;
  position: absolute;
  top: 16.5px;
  left: 40px;
  margin: 0 auto;
  border-style: solid;
  border-width: 12.5px 0 12.5px 20px;
  border-color: transparent transparent transparent rgba(255, 255, 255, 1);
}

.close-video::before {
  content: 'âœ–';
  cursor: pointer;
  position: absolute;
  right: 0;
  top: 0;
  font-size: 20px;
  padding: 20px;
  z-index: 1;
  line-height: .7;
  display: block;
  color: #fff;
}

.video-wrapper {
  position: relative;
  padding: 33.35% 0; /* 16:9 */
  height: 0;
}

.video-wrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}


.app .image-gallery,
.app-sandbox {
  margin: 0 auto;
  width: 65%;
  transition: all 1s ease;
}


@media (max-width: 1920px) {
  .app-sandbox-content {
    padding: 0 20px;
  }
}

.app-sandbox {
  margin: 40px auto;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}

.app-buttons li {
  display: block;
}

@media (max-width: 768px) {

  .app-header {
    font-size: 20px;
  }

  .app-buttons li {
    display: block;
    margin: 10px 0;
  }

  .app-buttons li + li {
    padding: 0;
  }

  .play-button {
    height: 40px;
    width: 65px;
  }

  .play-button:after {
    top: 11px;
    left: 27px;
    border-width: 8.5px 0 8.5px 12px;
  }

  .close-video::before {
    font-size: 16px;
    padding: 15px;
  }
}

@media (max-width: 1024px) {
  .app .image-gallery,
  .app-sandbox {
    width: 100%;
  }
}

.app-interval-input-group {
  display: table;
}

.app-interval-label {
  display: table-cell;
  vertical-align: middle;
  padding: 6px 12px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
  color: #555;
  text-align: center;
  background-color: #eee;
  border: 3px solid #ccc;
  border-right: none;
  border-radius: 4px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.app-interval-input {
  -webkit-appearance: none;
  display: table-cell;
  margin: 0;
  padding: 9px;
  border-radius: 5px;
  font-size: 14px;
  border: 3px solid #ccc;
  background: #fff;
  width: 100%;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

input.app-interval-input {
  width: 65px;
}

.app-checkboxes {
  margin-top: 10px;
}

.app-checkboxes li {
  display: block;
}

.image-gallery {
    width: 100%;
    height: 100%;
}

.image-gallery-slide {
  background-color: transparent;
}

.image-gallery-slide img {
    width: 100%;
    height: auto;
    max-height: calc(100vh - 160px);
    object-fit: contain;
    overflow: hidden;
    object-position: center center;
}

.image-gallery-thumbnails {
  padding: 2px;
  background-color: rgba(0, 0, 0, .5);
}

.image-gallery-thumbnail {
  border: none !important;
}

.image-gallery-thumbnails-wrapper {

}

.image-gallery-fullscreen-button {
  right: 0;
  top: 14px;
  transform: translateY(-50%);
  height: 15px;
}
